import "./styles.css";
import pic from "./images/musicLogo.png";
import React, { useState } from "react";

export default function App() {
  const genre = ["jazz", "pop", "rock"];
  const songsDict = {
    jazz: {
      "Dave Brubeck, “Take Five”": "3",
      "Miles Davis, “So What”": "4",
      "Duke Ellington, “Take The A Train”": "2"
    },
    pop: {
      "Toxic’ by Britney Spears": "4",
      "‘Firework’ by Katy Perry": "2",
      "‘Blinding Lights’ by The Weeknd": "5"
    },
    rock: {
      "‘Purple Haze’ by Jimi Hendrix": "4",
      "‘Baba O’Riley’ by The Who": "5",
      "‘Funk #49’ by the James Gang": "3"
    }
  };

  const [text, settext] = useState("pop");

  function onclickHandler(songs) {
    var list = songs;
    settext(list);
  }

  //var songList = Object.keys(songsDict[text]);
  var songList = Object.entries(songsDict[text]);
  //var ratingList = Object.values(songsDict[text]);

  console.log(text);
  console.log(songList);

  return (
    <div className="App">
      <div className="logo">
        <h1 style={{ display: "inline" }}>
          Song<span style={{ color: "#4f46e5" }}>Pick</span>
        </h1>
        <img className="logo-image" src={pic} alt="Logo"></img>
      </div>
      <p style={{ marginTop: "0px", fontSize: "x-large" }}>
        This is my library of songs
      </p>
      <ul className="genre-list" type="none">
        {genre.map(function (item) {
          return (
            <li
              key={item}
              onClick={() => onclickHandler(item)}
              className="genre-list-item"
            >
              {item}
            </li>
          );
        })}
      </ul>
      <hr></hr>
      <ul className="genre-list" type="none">
        {songList.map(([key, value]) => {
          return (
            <li className="songs-list">
              {key}
              <div>
                <span style={{ color: "green", fontSize: "large" }}>
                  {value}/5
                </span>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
}
